___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --primary-color: rgb(157, 0, 255);
    --primary-color-darker: rgb(43, 5, 67);
    --primary-color-dark: rgb(98, 29, 140);
    --primary-color-red: rgb(223, 12, 48);
    --primary-color-white: #fff;
}

* {
    box-sizing: border-box;
    outline: 0;

}

body {
    margin: 0;
    padding: 0;
    background: var(--primary-color);
    font-family: 'Open snas', sans-serif;
    font-size: 1.3em;
    line-height: 1.5em;
}

.container {
    max-width: 640px;
    margin: 50px auto;
    background: var(--primary-color-white);
    padding: 20px;
    border-radius: 20px;

}

.container h1{
    text-align: center;
    margin-bottom: 40px;
}

.input {
    font-size: 24px;
    height: 50px;
    padding: 0 20px;
    display: block;
    width: 100%;
    margin-bottom: 10px;
    border-radius: 10px;
}

.input:focus{
    outline: 1px solid var(--primary-color);
}

.button {
    border: none;
    background: var(--primary-color);
    color: var(--primary-color-white);
    font-size: 18px;
    font-weight: 700;
    height: 50px;
    cursor: pointer;
    width: 100%;
    margin-top: 30px;
    margin-bottom: 10px;
    border-radius: 10px;
}

.button:hover {
    background: var(--primary-color-darker);
}

.button:active {
    background: var(--primary-color-dark);
}

.cpf-gerado{
    font-size: 2em;
    color: var(--primary-color);
    text-align: center;
    font-weight: bold;
    margin-bottom: 40px;
    letter-spacing: 2px;
